<template>
    <div class="admin-list">
		
		
		<div style="margin-bottom:30px">	
	<el-breadcrumb separator="/">
  <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
  <el-breadcrumb-item></el-breadcrumb-item>
 
</el-breadcrumb>	
		
	</div>		
		
		
<el-form :inline="true"  class="demo-form-inline">
	
	 <el-form-item label="">
    <el-select v-model="state" @change="search" placeholder="">
      <el-option label="" value="-1"></el-option>
      <el-option label="" value="0"></el-option>
      <el-option label="/" value="1"></el-option>
      <el-option label="" value="100"></el-option>
	 <el-option label="" value="2"></el-option>
	 <el-option label="" value="4"></el-option>
    </el-select>
  </el-form-item>
	
	
	
	
	
	
  <el-form-item >
    <el-input v-model="order_num" placeholder=""></el-input>
  </el-form-item>
	  <el-form-item >
    <el-button type="primary" @click="search"></el-button>
  </el-form-item>
		
		
    <el-button @click="reload()" type="success"></el-button>
		
	
</el-form>
		
        <el-table
            v-loading='load'
            ref="multipleTable"
            @selection-change="handleSelectionChange"
            :data="tableData"
            border
            tooltip-effect="dark"
            style="width: 100%">
            <el-table-column
                type="selection">
            </el-table-column>

            <el-table-column
                prop="order_id"
                width=80
                label="">
            </el-table-column>

			<el-table-column
                prop="order_num"
                width="200"
                label="">
            </el-table-column>
			
            <el-table-column
                prop="username"
                label="">
            </el-table-column>
			
				
		   <el-table-column
                prop="order_price"
                label="">
            </el-table-column>

			<el-table-column
                prop="actually_paid"
                label="">
            </el-table-column>
			


            <el-table-column
                width="220"
               label="">
                <template scope="scope">
                    <el-icon name="time"></el-icon>
                    <span style="margin-left: 10px">{{ scope.row.create_time  }}</span>
                </template>
            </el-table-column>

            
			<el-table-column
                prop="express_num"
                width="180"
                label="">
            </el-table-column>
			
			
			
			<el-table-column
                prop="state_name"
                width="100"
                label="">
            </el-table-column>
			
			
			
            

            <el-table-column
                width="220"
               label="">
                <template scope="scope">
                    <el-icon name="time"></el-icon>
                    <span style="margin-left: 10px">{{ scope.row.express_time  }}</span>
                </template>
            </el-table-column>


            <el-table-column label="" fixed="right" width="300">
                <template scope="scope">
                    <el-button
                        v-if="scope.row.state ===1"
                        size="small"
                        type="primary"
                        @click="expressInput(scope.row)">{{scope.row.express_num?'':''}}
                    </el-button>
                    <el-button
                        size="small"
                        @click="editGoods(scope.row)">
                    </el-button>
                    <el-button
                        size="small"
                        type="danger"
                        @click="handleDelete(scope.row)">
                    </el-button>
                </template>
            </el-table-column>

        </el-table>
    
<div class="pagination">
	
	<el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="500">
	</el-pagination>
</div>

    </div>
</template>

<script>

    export default {
        name: 'list',
        data() {
            return {
               tableData: [],
				cur_page: 1,
				state: '',
				order_num:'',
                multipleSelection: [],

                load: false, // loading
            }
        },
        inject: ['reload'],
        methods: {
          
			fetchList() {
                this.load = true;
				var reqParams ={
					state:this.state,
					order_num:this.order_num,
                    cur_page :this.cur_page
				};
		console.log(this.state);
                this.func.ajaxPost(this.api.orderList,reqParams,res => {
                    
                    this.tableData = res.data.resultList;
                    console.log( res.data.resultList)
                    this.load = false;
                });
				
		
			},
			
		//
			handleCurrentChange(val) {
				this.cur_page = val;
				this.fetchList();
			},

			//
			search() {
                // this.reload();
				this.fetchList();
			},
  // 
            handleDelete(row) {
                this.func.ajaxPost(this.api.orderDelete, {order_id: row.order_id}, res => {
                    if (res.data.code === 200) {
                        let index = this.tableData.indexOf(row);
                        this.tableData.splice(index, 1);
                        this.$message.success('');
                    }
                });
            },


            // 
            editGoods (row) {
                this.$router.push({path: '/admin/order-form', query: {order_num: row.order_num,openid: row.openid}});
            },


            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            expressInput(row){
                this.$prompt('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                }).then(({ value }) => {
                    this.func.ajaxPost(this.api.orderExpress, {
                            order_num: row.order_num,
                            express_num:value
                        }, res => {
                            if (res.data.code === 200) {
                                this.$message({
                                    type: 'info',
                                    message: ''
                                });
                                this.reload();
                            }
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });       
                });
            },
        },

		
		created() {
			this.fetchList();
		},
      //
        dateFormat:function(row, column) {
               var date = row[column.property];
     if (date == undefined) {
        return "";
     }
     return moment(date).format("YYYY-MM-DD HH:mm:ss");
        }

    }
</script>


